import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)
const routes = [
    {
        path: '/',
        redirect: '/trend'
    },
    {
        path: '/home',
        name: 'Home',
        meta: {
            title: '首页'
        },
        component: () => import('../views/Home.vue')
    },
    {
        path: '/month/:id',
        name: 'MonthDetail',
        meta: {
            title: '月度明细'
        },
        component: () => import('../views/MonthDetail.vue')
    },
    {
        path: '/month/:id/day/:day',
        name: 'DayDetail',
        meta: {
            title: '日支出明细'
        },
        component: () => import('../views/DayDetail.vue')
    },
    {
        path: '/trend',
        name: 'trend',
        meta: {
            title: '趋势图'
        },
        component: () => import('../views/trend.vue')
    },
]

const router = new VueRouter({
    mode: 'hash',
    routes
})

router.beforeEach((to, from, next) => {
    next()
})

export default router
