import axios from 'axios'

const http = axios.create({
    // baseURL: process.env.VUE_APP_API_BASE_URL || 'http://localhost:8000/api', // 基础 URL
    timeout: 50000, // 请求超时时间
    headers: {
    }
})

// 请求拦截器
http.interceptors.request.use(
    config => {
        return config
    },
    error => {
        // 对请求错误做些什么
        return Promise.reject(error)
    }
)

// 响应拦截器
http.interceptors.response.use(
    response => {
        debugger

        return response
    },
    error => {

        return Promise.reject(error)
    }
)

export default http
